import clsx from "clsx";
import React, { useState } from "react";
import "../../styles/tailwind.css";

export interface ButtonProps {
  /** Is this the principal call to action on the page? */
  primary?: boolean;
  /** What background color to use */
  backgroundColor?: string;
  /** How large should the button be? */
  width?: number | string;
  height?: number | string;
  /** Button contents */
  label: string;
  /** Optional click handler */
  onClick?: () => void;
  rounded?: boolean;
  className?: string;
  color?: string;
}

/** Primary UI component for user interaction */
export const Button = ({
  primary = false,
  width,
  height,
  backgroundColor,
  label,
  rounded,
  className,
  color,
  ...props
}: ButtonProps) => {
  const [hover, setHover] = useState(false);

  return (
    <button
      onMouseOver={() => {
        setHover(true);
      }}
      onMouseLeave={() => {
        setHover(false);
      }}
      type="button"
      className={`${className} border-solid border-2 px-3 py-1 border-main-color text-main-color ${clsx(
        {
          [`text-white bg-main-color`]: !color && hover,
          [`text-white bg-[${color}]`]: color && hover,
          [`text-[${color}]`]: color,
          ["rounded"]: rounded,
        }
      )}`}
      style={{ backgroundColor, width: width, height: height }}
      {...props}
    >
      {label}
    </button>
  );
};
